// src/components/dashboard/TotalBalance.jsx
import { Card, CardContent } from "@/components/ui/card";
import { ChevronLeft, ChevronRight } from "lucide-react";
import { useState } from "react";

const accounts = [
  {
    type: "Credit Card",
    number: "**** **** **** 2598",
    balance: "$25,000",
    brand: "mastercard",
    color: "bg-teal-600",
  },
  {
    type: "Savings",
    number: "**** **** **** 7733",
    balance: "$5,000",
    brand: "visa",
    color: "bg-blue-600",
  },
  {
    type: "Wallet",
    number: "UPI - xxxxxx@ybl",
    balance: "$1,200",
    brand: "wallet",
    color: "bg-orange-500",
  },
];

export default function TotalBalance() {
  const [index, setIndex] = useState(0);

  const prev = () => setIndex((index - 1 + accounts.length) % accounts.length);
  const next = () => setIndex((index + 1) % accounts.length);

  return (
    <Card className="p-4 shadow-sm rounded-2xl bg-white">
      <div className="flex items-center justify-between mb-4">
        <h2 className="text-lg font-semibold">Total Balance</h2>
        <span className="text-gray-500 text-sm">All Accounts</span>
      </div>

      <CardContent className="relative">
        <div
          className={`rounded-xl p-6 text-white transition-all ${accounts[index].color}`}
        >
          <p className="text-sm">Account Type</p>
          <h3 className="text-xl font-semibold">{accounts[index].type}</h3>
          <p className="text-sm mt-1">{accounts[index].number}</p>
          <div className="mt-4 flex items-center justify-between">
            <span className="text-2xl font-bold">{accounts[index].balance}</span>
            <span className="text-xl">ðŸ’³</span>
          </div>
        </div>

        {/* Slider controls */}
        <button
          onClick={prev}
          className="absolute top-1/2 -left-3 transform -translate-y-1/2 bg-white rounded-full p-1 shadow"
        >
          <ChevronLeft className="w-5 h-5" />
        </button>
        <button
          onClick={next}
          className="absolute top-1/2 -right-3 transform -translate-y-1/2 bg-white rounded-full p-1 shadow"
        >
          <ChevronRight className="w-5 h-5" />
        </button>
      </CardContent>
    </Card>
  );
}
