// src/components/dashboard/RecentTransactions.jsx
import { Card, CardContent } from "@/components/ui/card";

const transactions = [
  { id: 1, title: "GTR 5", subtitle: "Gadget & Gear", amount: 160, date: "17 May 2025" },
  { id: 2, title: "Polo Shirt", subtitle: "XL fashions", amount: 20, date: "17 May 2025" },
  { id: 3, title: "Biriyani", subtitle: "Hajir Biriyani", amount: 10, date: "17 May 2025" },
  { id: 4, title: "Taxi Fare", subtitle: "Uber", amount: 12, date: "17 May 2025" },
  { id: 5, title: "Keyboard", subtitle: "Gadget & Gear", amount: 22, date: "17 May 2025" },
];

export default function RecentTransactions() {
  return (
    <Card className="p-4 shadow-sm rounded-2xl bg-white">
      <div className="flex items-center justify-between mb-4">
        <h2 className="text-lg font-semibold">Recent Transaction</h2>
        <button className="text-sm text-teal-600 font-medium">View All</button>
      </div>

      <CardContent className="space-y-4">
        <div className="flex gap-6 text-gray-600 font-medium">
          <span className="text-teal-600 border-b-2 border-teal-600 pb-1">All</span>
          <span>Revenue</span>
          <span>Expenses</span>
        </div>

        <div className="space-y-4">
          {transactions.map((tx) => (
            <div key={tx.id} className="flex items-center justify-between">
              <div>
                <p className="font-medium">{tx.title}</p>
                <p className="text-sm text-gray-500">{tx.subtitle}</p>
              </div>
              <div className="text-right">
                <p className="font-semibold">${tx.amount.toFixed(2)}</p>
                <p className="text-xs text-gray-400">{tx.date}</p>
              </div>
            </div>
          ))}
        </div>
      </CardContent>
    </Card>
  );
}
